function sliderWorkshop() {
    var swiper = new Swiper('.workshop .swiper', {
        slidesPerView: 3,
        spaceBetween: 47,
        navigation: {
            nextEl: ".workshop .swiper-button-next",
            prevEl: ".workshop .swiper-button-prev",
        },
        breakpoints: {
            320: {
                slidesPerView: 2,
                spaceBetween: 20,
            },
            767: {
                slidesPerView: 1.5,
            },
            992: {
                slidesPerView: 3,
                spaceBetween: 47,
            },
        }
    })
}

$(document).ready(function() {

    sliderWorkshop();

})